@import url(https://fonts.googleapis.com/css2?family=Tangerine:wght@700&display=swap);
* {
  background-color: #424949;
  
  margin: 0;
  padding: 0;
  box-sizing: border-box;
 
  font-family: "Itim", cursive;
}

.navbar {
  max-width: 100%;
  height: 80px;
  width: auto;
  color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  padding: 0 2rem;
  font-size: 1.2rem;

  background-color: #424949;
}

.nav-logo {
  text-decoration: none;
  color: white;
  font-size: 35px;
  font-family: "Tangerine", cursive;
}
.nav-logo:hover {
  text-decoration: none;
  color: #dc7633;
}

.nav-links {
  display: grid;
  grid-template-columns: repeat(6, auto);
  grid-gap: 20px;
  list-style: none;
}

.nav-link {
  text-decoration: none;
  background-color: #424949;

  color: white;
  transition: 0.3s all;
  font-size: 25px;
}

.nav-icon {
  display: none;
  font-size: 2rem;
  cursor: pointer;
}

@media only screen and (max-width: 600px) {
  .navbar {
    position: relative;
  }

  .nav-links {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    position: absolute;
    text-align: center;
    width: 100%;
    height: auto;
    top: 80px;
    left: -100%;
    transition: 0.5s all;
  }

  .nav-links.active {
    left: 0;
    z-index: 1;
  }
  .nav-item {
    padding: 20px 0;
  }
  .nav-icon {
    background: #1b4f72;
    display: -webkit-flex;
    display: flex;
  }
}

.Home-page {
  font-family: sans-serif;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.Developer-image {
  padding-left: 30px;
  border-radius: 20px;
  margin-top: 30px;
  height: 400px;
  width: auto;
  background-repeat: no-repeat;
  background-size: 250px 300px;
}
.Developer-details {
  height: 300px;
  width: 350px;

  border-radius: 20px;
  margin: 20px;
  text-align: center;
  padding: 30px;
}
.Developer-name {
  color: white;
}
.Developer-job {
  color: #ca6f1e;
}
.All-links {
  margin: 10px;
}
.linkedin:hover {
  color: #2689f8;
  border-radius: 100px;
  border: 3px solid #2689f8;
  fill: #2689f8;
  transition: all 0.2s;
}
.instagram:hover {
  border-radius: 100px;
  border: 3px solid #9a2970;
  fill: #9a2970;
  transition: all 0.2s;
}
.github:hover {
  border-radius: 100px;
  border: 3px solid black;
  fill: black;
  transition: all 0.2s;
}
.whatsapp:hover {
  border-radius: 100px;
  border: 3px solid #09c50b;
  fill: #09c50b;
  transition: all 0.2s;
}

.Resume-button {
  margin: 20px;
  padding: 16px 60px 16px 60px;
  border: none;
  border-radius: 20px;
  font-size: 15px;
  background: #d35400;
  color: white;
}

@media (max-width: 600px) {
  .Developer-image {
    height: 270px;
    width: 400px;
    padding-left: 12px;
  }
}

